import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getStudentInTORForGrading = ({
  torId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/TestOrganizing/GetStudentInTORForGrading',
    method: 'GET',
    params: {
      torId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getTestRoomForSelect = ({
  trainTypeId,
  facultyId,
  yearEduId,
  semesterId,
  subjectId,
  examBatch,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/TestOrganizing/GetTestRoomForSelect',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      yearEduId,
      semesterId,
      subjectId,
      examBatch,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getStudentInTO = ({
  tOId,
  tOrId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/TestOrganizing/GetStudentInTO',
    method: 'GET',
    params: {
      tOId,
      tOrId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const updateGradeByTOR = ({ torId, details }) =>
  request({
    prefix,
    url: `/TestOrganizing/UpdateGradeByTOR`,
    data: {
      torId,
      details,
    },
    method: 'PUT',
  });

export const setLockedByTORId = ({ torId, isLocked }) => {
  return request({
    prefix,
    url: `/TestOrganizing/SetLockedByTORId`,
    data: {
      torId,
      isLocked,
    },
    method: 'PUT',
  });
};

export const exportExcel = torId =>
  requestDownload({
    prefix,
    url: `/TestOrganizing/ExportExcelTORById`,
    data: {
      torId,
    },
    method: 'POST',
  });

export const exportPDF = torId =>
  requestDownload({
    prefix,
    url: `/TestOrganizing/ExportTORById`,
    data: {
      torId,
    },
    method: 'POST',
  });

export const getTestOrganizings = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  examBatch,
  examTime,
  testSessionId,
  subjectIds,
  fromDate,
  toDate,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/TestOrganizing`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      examBatch,
      examTime,
      testSessionId,
      subjectIds,
      fromDate,
      toDate,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });



