import React, { Component } from 'react';
import { Row, Col, Form, Input, Button } from 'antd';
import { SelectAjax } from 'components/common';
import { BtnAddNew, BtnDelete } from 'components/common/button';
import { getTestPackages } from 'services/testPackage';

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar1 extends Component {
  state = {};


  handleAdd = () => {
    this.props.handleAdd();
  }

  handleCreateTestPackage = () => {
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
      this.props.handleCreateTestPackage(values);
    });
  }

  handleDelete = () => {
    this.props.handleDelete();
  }

  handleDeleteTestPackage = () => {
    this.props.handleDeleteTestPackage()
  }

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
      this.props.handleSearch(values);
    });
  };

  handleSelectPackage = packageNo => {
    this.props.handleSearch(packageNo);
  }

  render() {
    const { isBtnAddDisable, isBtnRemoveDisable, isCreatingTestPackage  } = this.props;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Ti thi s">
              {getFieldDecorator('packageNo', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn ti thi!',
                  },
                ],
              })(
                <SelectAjax
                  service={getTestPackages}
                  onChange={this.handleSelectPackage}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          {/* <Col span={8}>
            <Form.Item label="HDSD">{getFieldDecorator('hdsd', {})(<Input />)}</Form.Item>
          </Col> */}
          <Col span={8}>
            <Form.Item label="K hiu phch">
              {getFieldDecorator('headSignal', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn k hiu phch!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="S phch t">
              {getFieldDecorator('fromNo', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn s phch!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <BtnAddNew title="Thm sinh vin" onClick={this.handleAdd} disabled={isBtnAddDisable} />
              <BtnDelete title="Xo sinh vin" onClick={this.handleDelete} disabled={isBtnRemoveDisable} />
            </div>
            <div className="btn-group" style={{ float: 'right' }}>
              <Button type="primary" onClick={this.handleCreateTestPackage} loading={isCreatingTestPackage} >Lp s phch </Button>
              <BtnDelete title="Xo" onClick={this.handleDeleteTestPackage} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar1;



