import React, { Component } from 'react';
import { Row, Col, Form, InputNumber } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
// import { getTestPackages } from 'services/testPackage';
import { getListSubject } from 'services/Subject';
import { getTestRoomForSelect } from 'services/testOrganizing';

// const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
      this.props.handleSearch(values);
    });
  };

  test = async torid => {
    const { getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const subjectId = getFieldValue('subjectId');
    const examBatch = getFieldValue('examBatch');
    const res = await getTestRoomForSelect({ trainTypeId, facultyId, yearEduId, semesterId, subjectId, examBatch });
    if(res.data && res.data.items) {
      const { toId } = res.data.items.filter(item => item.value === torid)[0]
      this.props.selectToId(toId);
    }
    console.log(torid);
    console.log(res);
  }
  
  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const subjectId = getFieldValue('subjectId');
    const examBatch = getFieldValue('examBatch');
    console.log(examBatch);
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn hc phn',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="H">
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={6}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('subjectId', optionFieldDecorator.subjectId)(
                <SelectAjax
                  service={getListSubject}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId }}
                  allowGetData={!!trainTypeId && !!facultyId && !!yearEduId && !!semesterId}
                />
              )}
            </Form.Item>
          </Col>
          {/* <Col span={6}>
            <Form.Item label="Ln thi">
              {getFieldDecorator('system4', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn ln thi!',
                  },
                ]
              })(
                <Select>
                  <Option value=" 1">Thi ln 1</Option>
                  <Option value=" 2">Thi ln 2</Option>
                  <Option value=" 3">Thi ln 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col> */}
          <Col span={6}>
            <Form.Item label="t thi">
              {getFieldDecorator('examBatch', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn t thi!',
                  },
                ],
              })(
                <InputNumber />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phng thi">
              {getFieldDecorator('tOrId', {})(
                <SelectAjax
                  service={getTestRoomForSelect}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId, subjectId, examBatch }}
                  allowGetData={!!trainTypeId && !!facultyId && !!yearEduId && !!semesterId && !!subjectId && !!examBatch}
                  keyValue="value"
                  onChange={this.test}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch onClick={this.handleSearch} />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



