import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Col, message } from 'antd';
import { isEmpty } from 'lodash';
import Page from 'components/LayoutComponents/Page';
// import { BtnAddNew, BtnDelete } from 'components/common/button';
import { getStudentInTO } from 'services/testOrganizing';
import { getStudentInTestPackage, createTestPackage, deleteTestPackage } from 'services/testPackage';
// import { BtnEdit, BtnDelete } from 'components/common/button';
// import { Link } from 'react-router-dom';
// import routeConstant from 'routes/route/routeConstant';
import SearchBar from './SearchBar';
import SearchBar1 from './SearchBar1';

// import { policyObjects } from 'dump/data';

const column1 = [
  {
    title: 'S bo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'M sinh vin',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'H v tn',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Phng thi',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Ghi ch',
    dataIndex: 'note',
    key: 'note',
  },
];
const column2 = [
  {
    title: 'S bo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'S phch',
    dataIndex: 'detachHeadNo',
    key: 'detachHeadNo',
  },
  {
    title: 'H v tn',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Phng thi',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Ghi ch',
    dataIndex: 'note',
    key: 'note',
  },
];

class ListCadencePage extends Component {
  state = {
    tableData: [],
    tableData2: {},
    selectedAddRows: [],
    selectedRemoveRows: [],
    selectedRowKeys: [],
    tOId: false,
    packageNo: false,
    subjectId: false,
    isSearchStudentInTO: false,
    isSearchingStudentInTP: false
  };

  addItems = () => {
    const  tableData2 = {...this.state.tableData2};
    console.log(tableData2);
    this.state.selectedAddRows.forEach(item => {
      tableData2[item.id] = item;
    });
    this.setState({ tableData2 });
  }

  handleCreateTestPackage = async ({ packageNo, headSignal, fromNo }) => {
    try {
      this.setState({ isCreatingTestPackage: true })
      const { subjectId } = this.state;
      const students = {...this.state.tableData2};
      let count = Number(fromNo) - 1;
      const resquestData = {
        toId: this.state.tOId,
        packageNo,
        headSignal,
        fromNo
      };
      resquestData.students = Object.entries(students).map(studentEntry => {
        count += 1;
        return ({
          ...studentEntry[1],
          toStudentId: studentEntry[1].id,
          tpId: packageNo,
          isLocked: false,
          subjectId,
          detachHeadNo: `${headSignal}${count}`
        })
      })

      await createTestPackage(resquestData);
      this.handleSearchStudentInTP(this.state.packageNo);
      message.success('Lp s phch thnh cng');
    } catch (err) {
      console.log(err);
      message.success('C li xy ra!');
    } finally {
      this.setState({ isCreatingTestPackage: false })
    }
  }

  handleDeleteTestPackage = async () => {
    try {
      const { tOId, packageNo } = this.state;
      if(tOId && packageNo) {
        await deleteTestPackage({ toId: tOId, packageNo });
        message.success('Xo thnh cng');
        this.handleSearchStudentInTP(this.state.packageNo);
      } else {
        message.warn('Vui lng chn phng thi v ti thi!');
      }
    } catch (err) {
      message.error('C li xy ra');
    }
  }

  handleSearch = async ({ tOrId, subjectId }) => {
    try {
      this.setState({ subjectId, isSearchStudentInTO: true });
      const res = await getStudentInTO({ tOrId });
      if(res.data && res.data.items) {
        this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      message.error('C li xy ra!');
    } finally {
      this.setState({ isSearchStudentInTO: false });
    }
  }

  handleSearchStudentInTP = async packageNo => {
    try {
      this.setState({ packageNo, isSearchingStudentInTP: true });
      const res = await getStudentInTestPackage({ packageNo, tOId: this.state.tOId });
      console.log(res);
      if(res.data && res.data.items) {
        const tableData2 = {};
        res.data.items.forEach(item => {
          tableData2[item.toStudentId] = { ...item, id: item.toStudentId };
        })
        this.setState({ tableData2 });
      }
    } catch (err) {
      console.log(err);
    } finally {
      console.log("finally");
      this.setState({ isSearchingStudentInTP: false });
    }
  }

  handleSelectToId = tOId => {
    this.setState({ tOId });
  }

  removeItems = () => {
    const  tableData2 = {...this.state.tableData2};

    this.state.selectedRemoveRows.forEach(item => {
      delete tableData2[item.id];
    });

    this.setState({ tableData2, selectedRowKeys: [] });
  }

  render() {
    const {
      tableData,
      tableData2,
      isSearchStudentInTO,
      selectedAddRows,
      selectedRemoveRows,
      isSearchingStudentInTP,
      isCreatingTestPackage
    } = this.state;
    const isBtnAddDisable = isEmpty(selectedAddRows);
    const isBtnRemoveDisable = isEmpty(selectedRemoveRows);
    this.rowSelection1 = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.setState({ selectedAddRows: selectedRows });
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };

    this.rowSelection2 = {
      selectedRowKeys: this.state.selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.setState({ selectedRemoveRows: selectedRows, selectedRowKeys });
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    return (
      <Fragment>
        <Card title="nh phch">
          <Row>
            <Col span={24}>
              <SearchBar handleSearch={this.handleSearch}  selectToId={this.handleSelectToId} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                rowSelection={this.rowSelection1}
                bordered
                dataSource={tableData}
                columns={column1}
                loading={isSearchStudentInTO}
              />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sch nh phch">
          <SearchBar1
            handleSearch={this.handleSearchStudentInTP}
            handleAdd={this.addItems}
            handleDelete={this.removeItems}
            handleCreateTestPackage={this.handleCreateTestPackage}
            handleDeleteTestPackage={this.handleDeleteTestPackage}
            isSearchingStudentInTP={isSearchingStudentInTP}
            isCreatingTestPackage={isCreatingTestPackage}
            isBtnAddDisable={isBtnAddDisable}
            isBtnRemoveDisable={isBtnRemoveDisable}
          />
          <Row>
            <Col span={24}>
              <Table
                rowSelection={this.rowSelection2}
                bordered
                dataSource={Object.entries(tableData2).map(tableEntry => tableEntry[1])}
                columns={column2}
                loading={isSearchingStudentInTP}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCadencePage />
  </Page>
);



